  gotoFinalizeProposalPage = () => {
    this.props.history.push('#/underwriting/finalize-proposal/approver/1');
    window.location.reload();
  };

  queryStringToJSON = queryString => {
    if (queryString.trim().length < 1) {
      return {};
    }

    var pairs = queryString.slice(1).split('&');

    var result = {};
    pairs.forEach(function (pair) {
      pair = (pair as string).split('=');
      result[pair[0]] = decodeURIComponent(pair[1] || '');
    });

    return JSON.parse(JSON.stringify(result));
  };

  componentDidMount = () => {
    let propsJson = JSON.parse(JSON.stringify(this.props));
    let queryValues = this.queryStringToJSON(propsJson.location.search);
    console.log('url json: ', propsJson);
    console.log('url query: ', queryValues);
    this.setState({
      role:
        queryValues && queryValues.role
          ? queryValues.role
          : 'underwriting_staff',
    });

    // localStorage.setItem('sidebar', 'dashboard');
  };



  handleViewTicket = (caseid: any) => {
    console.log(caseid)
    Processmaker.getWithToken('cases/' + caseid.app_uid, '').then(
      response1 => {
        if (
          response1 !== undefined &&
          response1.current_task !== undefined
        ) {
          const app_index = response1.current_task[0].del_index;
          Processmaker.getWithToken(
            'cases/' +
            caseid.app_uid +
            '/variables?app_index=' +
            app_index,
            '',
          ).then(response2 => {
            let clientId = '';
            let contractId = '';
            if (response2 !== undefined &&
              response2.mother_contract_id !== undefined
            ) {
              contractId = response2.mother_contract_id;
            }
            if (response2 !== undefined &&
              response2.client_id !== undefined
            ) {
              clientId = response2.client_id;
            }
            switch (caseid.app_tas_title) {
              case APPROVE_PROPOSAL_WORKFLOW:
                this.setState({
                  redirect: true,
                  redirect_info: {
                    pathname:
                      '/underwriting/approve-proposal/' +
                      clientId +
                      '/' +
                      contractId +
                      '/' +
                      caseid.app_uid,
                  },
                });
                break;
              case FINALIZED_PROPOSAL_WORKFLOW:
                this.setState({
                  redirect: true,
                  redirect_info: {
                    pathname:
                      '/underwriting/finalize-proposal/' +
                      clientId +
                      '/' +
                      contractId +
                      '/approver/' +
                      caseid.app_uid,
                  },
                });
                break;
            };
          }).catch(error => {

            console.log('getbenefitlist api error: ', error);
          });
        }
      });
  }

